import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave } from 'components/common/button';
import { Card, Row, Table, Col, Tag } from 'antd';
import { commentEndCourse, papers } from 'dump/data';
import FormSearchStudent from './FormSearchStudent';

const columns = [
  {
    title: 'Mã SV',
    dataIndex: 'masv',
    key: 'name',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'nameEn',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'fromScores',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'toScores',
  },
];

const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: record.status === 'Đủ', // Column configuration not to be checked
    name: record.name,
  }),
};

const paperColumns = [
  {
    title: 'Trạng thái',
    dataIndex: 'status',
    key: 'code',
    render: status => (
      <Tag color={status === 'Thiếu' ? 'volcano' : 'green'} key={status}>
        {status}
      </Tag>
    ),
  },
  {
    title: 'Bắt buộc',
    dataIndex: 'isRequire',
    key: 'code',
  },
  {
    title: 'Tên giấy tờ',
    dataIndex: 'name',
    key: 'code',
  },
  {
    title: 'Số lượng',
    dataIndex: 'number',
    key: 'code',
  },
];

class ReceiveAdmissionPapersPage extends Component {
  state = {};

  render() {
    return (
      <Fragment>
        <Card title="Nhận giấy tờ bổ sung">
          <Row>
            <Col span={24}>
              <FormSearchStudent />
            </Col>
            <Col span={24}>
              <Table
                rowSelection={rowSelection}
                bordered
                dataSource={commentEndCourse}
                columns={columns}
              />
            </Col>
          </Row>
        </Card>
        <Card title="Thông tin giấy tờ">
          <Row>
            <Col span={24}>
              <Row>
                <Table
                  rowSelection={rowSelection}
                  bordered
                  dataSource={papers}
                  columns={paperColumns}
                />
              </Row>
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiveAdmissionPapersPage />
  </Page>
);



